		   				})
		   			}
		   		},
		   		fail(res){
		   			uni.showToast({
		   				icon:"none",
		   				title:"取消收藏失败"
		   			})
		   		}
		   	})
		   						
		   }
		   else{
		   
		   	uni.request({
		   		url:that.$server+"/user-knowledge/userFavor?kid="+kid+"&favor=1",
