									list2[i].userAnswer="";
									this.subjectList.push(list2[i]);

								}
								var list = this.initList(list2);
								for(var j=0;j<list.length;++j)
								{
									this.audiolist.push(list[j]);
								}
								
								
								
								
							
							}
					}
					}
					
