		loadData:async function(){
			uni.showLoading({
				title:"加载中"
			});
			var url=this.$server+"/knowledge-basic/get?pn="+this.pn+"&ps=20&cid="+this.cid;
			var [error,res] = await uni.request({
				url:url ,
				withCredentials:true,
				header:{'Cookie':"JSESSIONID="+this.JSESSIONID()},
				
			});
			
			if(res!=undefined && res.data.code==200 && res.data.data!=null && res.data.data.length>0){
			
			var data=res.data.data;
			for(var i=0;i<data.length;++i)
			{
				data[i].userAnswer="";
				data[i].userFavor=0;
			}
			this.subjectList = res.data.data;
			const list = this.initList(this.subjectList);
			this.curPlayIndex = 0;
			
			this.setAudiolist(list);
			this.initPlay(0);
			//列表延后渲染
			setTimeout(()=>{
				this.copyAudioList = list;
			},1000)
			
			
			uni.hideLoading();
			this.flag=true;
			
		}else{
			uni.hideLoading();
			uni.showToast({
				icon:"none",
				title:"出现问题了"
			})
		}
